body {
    margin: 0;
    padding: 0;
    background-color: #D282A6;
  }
  
.container {
    padding: 20px;
    background-color: #F5E3E0;
    text-align: center;
  }
  
  h1 {
    color: #6E4555;
    font-family: Georgia, 'Times New Roman', Times, serif;
    align-items: center;
    font-size: 24px;
    margin-bottom: 10px;
    text-shadow: 1px 1px 1px #D282A6;
  }
  
   
  img {
    width: 300px;
    height: 200px;
    object-fit: cover;
    margin-bottom: 10px;
    border-radius: 4px;
    box-shadow: 0 2px 4px #E8B4BC;
    padding: 10px;
  }
  
  form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  select,
  input {
    margin-bottom: 10px;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 300px;
  }
  
  button {
    padding: 10px 20px;
    background-color: #6E4555;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #D282A6;
  }