.list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.menuCard {
  display: flex;
  flex-direction: column;
  justify-content: first baseline;
  background-color: #E8B4BC;
  border: 1px solid #6E4555;
  border-radius: 4px;
  padding: 10px;
  margin-bottom: 10px;
  box-shadow: 0 2px 4px #D282A6;
  position: relative;
  width: 350px;
  height: 450px;
  flex-wrap: wrap;
  align-items: center;  
}

.roomNumber,
.breakfast {
  font-size: 20px;
  font-family:Georgia, 'Times New Roman', Times, serif;
  color: #3A3238;
  margin: auto;
}

.icons {
  align-items: center;
}